:root {
  --color-header-black: #050505;
  --color-header-green: #9bb537;
  --color-white: #fafafa;
  --color-white-secondary: #fff;
}

.list {
  list-style: none;
}

.header-container {
  justify-content: space-between;
  align-items: center;
  padding: 20px 0 2px;
  display: flex;
}

@media screen and (min-width: 768px) {
  .header-container {
    padding-top: 44px;
  }
}

.header-nav, .header-logo {
  display: flex;
}

@media screen and (min-width: 768px) {
  .header-logo {
    align-items: center;
    margin-left: 203px;
  }
}

@media screen and (min-width: 1280px) {
  .header-logo {
    margin-left: 391px;
  }
}

.links {
  text-decoration: none;
}

.header-logo-text {
  letter-spacing: -.04em;
  text-align: center;
  color: inherit;
  font-size: 16px;
  font-weight: 600;
  line-height: 1.125;
}

@media screen and (min-width: 1280px) {
  .header-logo-text {
    letter-spacing: -4%;
    line-height: 1.11111;
  }
}

.header-nav-list {
  padding: 0;
}

@media screen and (max-width: 767px) {
  .header-nav-list {
    display: none;
  }
}

@media screen and (min-width: 768px) {
  .header-nav-list {
    gap: 16px;
    display: flex;
  }
}

.header-logo-accent {
  color: var(--color-header-green);
}

.header-link-home:hover, .header-link-home:focus, .header-link-home:active, .header-link-favorites:hover, .header-link-favorites:focus, .header-link-favorites:active {
  fill: var(--color-header-green);
  text-decoration: underline;
}

.burger-menu-open-btn {
  background-color: #0000;
  border: none;
  padding: 0;
  line-height: 0;
}

@media screen and (min-width: 768px) {
  .burger-menu-open-btn {
    display: none;
  }
}

.burger-menu-open-btn-icon {
  stroke: var(--color-header-black);
}

.burger-menu-open-btn-icon:hover, .burger-menu-open-btn-icon:focus {
  stroke: var(--color-header-green);
}

.shopping-btn {
  cursor: pointer;
  background-color: #0000;
  border: none;
  padding: 0;
  line-height: 0;
}

.shopping-btn-icon {
  stroke: var(--color-header-black);
  fill: var(--color-white);
}

.shopping-btn-icon:hover, .shopping-btn-icon:focus {
  stroke: var(--color-header-green);
}

.header-bloc-btn {
  gap: 14px;
  display: flex;
}

@media screen and (min-width: 1280px) {
  .header-bloc-btn {
    gap: 24px;
  }
}

.header-theme-checkbox {
  display: none;
}

@media screen and (min-width: 768px) {
  .header-theme-checkbox {
    cursor: pointer;
    display: block;
  }
}

.header-theme-checkbox input {
  cursor: pointer;
}

@media screen and (max-width: 319px) {
  .mobile-menu {
    display: none;
    position: relative;
  }
}

@media screen and (min-width: 320px) {
  .mobile-menu-close-btn {
    background-color: #0000;
    border: none;
  }

  .mobile-menu-close-btn-icon {
    stroke: #f8f8f8;
  }

  .mobile-menu {
    z-index: 100;
    opacity: 0;
    visibility: hidden;
    pointer-events: none;
    background-color: #9bb537;
    width: 100vw;
    min-height: 812px;
    transition: transform .25s cubic-bezier(.4, 0, .2, 1), opacity .25s cubic-bezier(.4, 0, .2, 1), visibility .25s cubic-bezier(.4, 0, .2, 1);
    position: fixed;
    top: 0;
    right: 0;
    transform: translate(100%);
  }

  .mobile-menu.is-open {
    opacity: 1;
    visibility: visible;
    pointer-events: auto;
    transform: translate(0);
  }

  .mobile-menu-container {
    text-align: center;
    flex-direction: column;
    height: 100vh;
    padding-top: 136px;
    padding-bottom: 20px;
    padding-left: 20px;
    display: flex;
    position: relative;
  }

  .mobile-menu-close-btn {
    border: none;
    border-radius: 50%;
    justify-content: center;
    align-items: center;
    width: 32px;
    height: 32px;
    padding: 0;
    transition: color .25s cubic-bezier(.4, 0, .2, 1);
    display: flex;
    position: absolute;
    top: 20px;
    right: 20px;
  }

  .mobile-nav-item {
    text-align: center;
  }

  .mobile-nav-list {
    flex-direction: column;
    justify-content: center;
    gap: 24px;
    display: flex;
  }

  .mobile-nav-link.link {
    color: #fff;
  }

  .mobile-nav-link {
    font-weight: 500;
    line-height: 1.14286;
    transition: color .25s cubic-bezier(.4, 0, .2, 1);
    display: inline-block;
    position: relative;
  }

  .mobile-nav-link:after {
    content: "";
    border: 1px solid #0000;
    width: 24px;
    height: 0;
    transition: border-color .25s cubic-bezier(.4, 0, .2, 1);
    position: absolute;
    top: 25px;
    left: 0;
  }
}

.modal-theme-checkbox {
  align-items: center;
  display: flex;
  position: absolute;
  bottom: 20px;
  left: 20px;
}

.modal-link-home, .modal-link-favorites {
  color: #f8f8f8;
}

.categories-wrap {
  width: 100%;
}

.categories-btn {
  color: #05050580;
