  border-radius: 12px;
}

@media screen and (min-width: 768px) {
  .categories-bloc {
    width: 176px;
  }

  .categories-btn {
    max-width: 176px;
    max-height: 50px;
  }

  .categories-list {
    flex-direction: column;
    gap: 18px;
    height: 416px;
    display: flex;
  }

  .categories-scrollbar::-webkit-scrollbar {
    height: 201px;
  }
}

@media screen and (min-width: 1280px) {
  .categories-bloc {
    width: 235px;
  }

  .categories-list {
    gap: 24px;
    width: 100%;
    height: 416px;
  }

  .categories-scrollbar {
    overflow-y: scroll;
  }

  .categories-scrollbar::-webkit-scrollbar {
    height: 201px;
  }
}

