:root {
  --footer-bg-color: #9bb537;
  --footer-text-color: #50e043;
  --footer-link-color: #292424;
  --btn-to-top-bg-color: #f46767;
  --btn-to-top-fill-color: #35f2c9;
  --focus-visible-box-shadow-color: #00000080;
  --btn-to-top-hover-bg-color: #f80f23;
  --muted-text-clr: #a9a9a9;
}

.footer, .footer_container {
  justify-content: center;
  align-items: center;
  padding-top: 40px;
  padding-bottom: 30px;
  display: flex;
}

.footer_desc, .footer_draw-text {
  display: inline-block;
}

.show-btn {
  background-color: var(--btn-to-top-bg-color);
  fill: var(--footer-bg-color);
  text-align: center;
  cursor: pointer;
  z-index: 100;
  opacity: .5;
  border: none;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 40px;
  height: 40px;
  font-size: 16px;
  display: none;
  position: fixed;
  bottom: 10px;
  right: 10px;
  transform: translateY(10px);
}

.show-btn.is-visible {
  opacity: .6;
  display: flex;
  transform: translateY(0);
}

.show-btn:hover, .show-btn:focus {
  background-color: var(--btn-to-top-hover-bg-color);
  box-shadow: 0 0 0 3px var(--focus-visible-box-shadow-color);
  transform: scale(1.05);
}

.show-btn:active {
  filter: brightness(1.2);
}

.footer_icon {
  animation: 1s ease-in-out infinite pulse;
}

@keyframes pulse {
  0% {
    transform: scale(1);
  }

  50% {
    transform: scale(1.2);
  }

  100% {
    transform: scale(1);
  }
}

.footer_link {
  color: var(--footer-link-color);
  text-align: center;
  cursor: pointer;
  background-color: #0000;
  border: none;
  padding: 10px 20px;
  transition: transform .3s, color .3s;
  display: inline-block;
  position: relative;
}

.footer_link:hover, .footer_link:focus {
  color: var(--footer-bg-color);
  outline: none;
  transform: translateY(-5px);
}

.footer_link:active {
  transform: translateY(0);
}

@media screen and (min-width: 320px) {
  .show-btn {
    width: 40px;
    height: 40px;
  }
}

@media screen and (min-width: 768px) {
  .show-btn {
    width: 50px;
    height: 50px;
  }

  .footer, .footer_container {
    padding-top: 60px;
    padding-bottom: 40px;
  }
}

@media screen and (min-width: 1280px) {
  .show-btn {
    width: 55px;
    height: 55px;
  }

  .footer, .footer_container {
    padding-top: 50px;
    padding-bottom: 30px;
  }
}

@media (max-width: 767px) {
  .footer, .footer_container {
    flex-direction: column;
    padding-top: 60px;
    padding-bottom: 40px;
  }
}

