


.backdrop__rating {
    position: fixed;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(5, 5, 5, 0.2);
  transition: opacity 250ms cubic-bezier(0.23, 0.86, 0.79, 0.77);
  z-index: 5;
}
.is-hidden {
    white-space: nowrap;
    clip-path: inset(100%);
    clip: rect(0 0 0 0);
    border: 0;
    width: 1px;
    height: 1px;
    margin: -1px;
    padding: 0;
    position: absolute;
    overflow: hidden;
  }
  .backdrop__rating.is-hidden .rating__modal {
    transform: translate(-50%, -50%) scale(0);
}
.rating__modal {
    width: 335px;
  height: 216px;
  padding: 20px;
  border-radius: 15px;

  background-color: #fff;

  position: absolute;

  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%) scale(1);

  transition: transform 250ms cubic-bezier(0.23, 0.86, 0.79, 0.77);
}

@media screen and (max-width: 768px) {
    .rating__modal {
        min-width: 335px;
        height: 216px;
        padding: 20px;
    }
}



.rating-text {
    font-weight: 600;
}

.rating__wrap {
    position: relative;
  display: flex;
  align-items: center;
  margin-bottom: 24px;
  margin-top: 4px;
}

.set__rating .rating__value {
    font-size: 16px;
  line-height: 1.5;
  margin-right: 8px;
}

.rating__radio {
    width: 24px;
    height: 24px;
    opacity: 0;
}

.star__wrap {
  position: absolute;
  left: 32px;
}

.rating__icon {
    fill: #d9d9d9;
}

.active__star {
    fill: #eea10c;
}
.active_star {
    fill: #eea10c;
  }
.rating__input {
    height: 46px;
  border: 1px solid rgb(5, 5, 5, 0.2);
  border-radius: 15px;
  display: block;
  width: 100%;
  padding: 14px 18px;
  margin-bottom: 14px;
}

.rating__submit {
    width: 100%;
    border-radius: 15px;
    background: #9bb537;
    border: 1px solid #9bb537;
    padding: 14px 28px;
    color: #f8f8f8;
}

.close__rating__btn {
    border: none;
  position: absolute;
  right: 14px;
  top: 14px;
  background-color: rgba(0, 0, 0, 0);
}

.close__icon {
    width: 12px;
  height: 12px;
  flex-shrink: 0;
}

.card__rating,
.popup__rating {
    display: flex;
    gap: 4;
}

.card__rating .rating__icon {
    width: 18px;
    height: 18px;
    fill-opacity: 0.1;
}

@media screen and (min-width: 768px) and (max-width: 1280px) {
    .card_rating .rating_icon {
      width: 16px;
      height: 16px;
    }
    .rating_modal {
        width: 424px;
        height: 254px;
        padding: 32px;
      }
      .card_rating .active_star {
        fill-opacity: 1;
      }
      


} 